import React from 'react';
import Modal from 'react-modal';
import "./styles.scss";
import Icon from "@material-ui/core/Icon";

interface WarningOrderModalProps {
  visible?: boolean
  onClose?: () => void
}

const WarningOrderModal = (props: WarningOrderModalProps) => {
  return (
    <Modal
      isOpen={props.visible ?? false}
      onRequestClose={props.onClose}
      style={customStyles}
    >
      <div className="Main-Pressable-Container" onClick={props.onClose}>
        <div className="Icon-Wrapper">
          <Icon fontSize="large" className="Icon-Center">checkcircle</Icon>
        </div>
      </div>
    </Modal>
  )
}

const customStyles = {
  content: {
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    padding: 0,
    margin: 0,
    backgroundColor: 'transparent',
    borderWidth: 0,
  },
};

export default WarningOrderModal