import { useState, useEffect } from "react";
import { firestore } from "../../common/firebase";
import alert from "../../assets/music/alert.wav";

let length = 0;
let audio: HTMLAudioElement | null = null;

const useAudio = () => {
  const [visible, setVisible] = useState<boolean>(false);
  const [ready, setReady] = useState<boolean>(false);

  const listener = () => {
    const ref = firestore.collection("orders").onSnapshot((snapshot) => {
      if (snapshot.docs.length > length && !!audio) {
        console.log(" PLAY ");

        if (audio.duration > 0 && !audio.paused) {
          audio.pause();
          audio.currentTime = 0;
          audio.play();
        } else {
          audio.play();
        }

        audio.currentTime = 0;
        audio.play();

        length = snapshot.docs.length;
        setVisible(true);
      }
    });

    return ref;
  };

  const init = async () => {
    const ref = await firestore.collection("orders").get();

    const notify = new Audio(alert);

    length = ref.docs.length;
    audio = notify;
    setReady(true);
  };

  useEffect(() => {
    init();
  }, []);

  useEffect(() => {
    if (ready) {
      listener();
    }
  }, [ready]);

  const pause = () => {
    audio && audio.pause();
    setVisible(false);
  };

  return {
    visible,
    actions: { pause },
  };
};

export default useAudio;
