import { put, call, takeLatest, all } from "redux-saga/effects";
import {
  requestBlockUser,
  requestBlockUserError,
  requestBlockUserSuccess,
  requestGetUsers,
  requestGetUsersError,
  requestGetUsersSuccess,
  requestSingleUser,
  requestSingleUserError,
  requestSingleUserSuccess,
  requestUpdateUser,
  requestUpdateUserError,
  requestUpdateUserSuccess,
  fetchGetUsers as fetchGetUsersAction,
} from "./actions";
import {
  FETCH_BLOCK_USER,
  FETCH_GET_USERS,
  FETCH_SINGLE_USER,
  FETCH_UPDATE_USER,
} from "./constants";
import {
  BlockUserAction,
  FetchBlockUser,
  FetchSingleUser,
  FetchUpdateUser,
  GetUsersAction,
  SingleUserAction,
  UpdateUserAction,
} from "./types";
import { firestore } from "../../common/firebase";

function* fetchBlockUser(data: FetchBlockUser) {
  try {
    yield put(requestBlockUser());

    yield call(() =>
      firestore
        .collection("additional_users")
        .doc(data.userId)
        .update({ blocked: data.blocked })
    );

    yield put(requestBlockUserSuccess());
    yield put(fetchGetUsersAction());
  } catch (error) {
    console.log(error);
    yield put(requestBlockUserError());
  }
}

export function* watchFetchBlockUser() {
  yield takeLatest<BlockUserAction>(FETCH_BLOCK_USER, fetchBlockUser);
}

function* fetchGetUsers() {
  try {
    yield put(requestGetUsers());

    const users = yield call(() =>
      firestore
        .collection("additional_users")
        .get()
        .then((querySnapshot) => {
          const resData: any = [];

          querySnapshot.forEach((doc) => {
            resData.push({
              _id: doc.id,
              ...doc.data(),
            });
          });

          return resData;
        })
    );

    yield put(requestGetUsersSuccess(users));
  } catch (error) {
    console.log(error);
    yield put(requestGetUsersError());
  }
}

export function* watchFetchGetUsers() {
  yield takeLatest<GetUsersAction>(FETCH_GET_USERS, fetchGetUsers);
}

function* fetchSingleUser(data: FetchSingleUser) {
  try {
    yield put(requestSingleUser());

    const user = yield call(() =>
      firestore
        .collection("additional_users")
        .doc(data.userId)
        .get()
        .then((doc) => {
          return {
            _id: doc.id,
            ...doc.data(),
          };
        })
    );

    yield put(requestSingleUserSuccess(user));
  } catch (error) {
    console.log(error);
    yield put(requestSingleUserError());
  }
}

export function* watchFetchSingleUser() {
  yield takeLatest<SingleUserAction>(FETCH_SINGLE_USER, fetchSingleUser);
}

function* fetchUpdateUser(data: FetchUpdateUser) {
  try {
    yield put(requestUpdateUser());

    yield call(() =>
      firestore.collection("additional_users").doc(data.userId).update({
        discount: data.discount,
      })
    );

    yield put(requestUpdateUserSuccess());
    data.history.goBack();
  } catch (error) {
    console.log(error);
    yield put(requestUpdateUserError());
  }
}

export function* watcFetchUpdateUser() {
  yield takeLatest<UpdateUserAction>(FETCH_UPDATE_USER, fetchUpdateUser);
}
