import { all, call, put, select, takeLatest } from "redux-saga/effects";
import { database, firestore } from "../../common/firebase";
import {
  requestAllOrders,
  requestAllOrdersError,
  requestAllOrdersSuccess,
  requestSingleOrder,
  requestSingleOrderError,
  requestSingleOrderSuccess,
  requestUpdateOrderStatus,
  requestUpdateOrderStatusError,
  requestUpdateOrderStatusSuccess,
  fetchAllOrders as fetchAllOrdersAction,
  fetchSingleOrder as fetchSingleOrderAction,
} from "./actions";
import {
  FETCH_ALL_ORDERS,
  FETCH_SINGLE_ORDER,
  FETCH_UPDATE_ORDER_STATUS,
} from "./constants";
import {
  AllOrdersAction,
  FetchSingleOrder,
  FetchUpdateOrderStatus,
  SingleOrderAction,
  UpdateStatusOrderAction,
} from "./types";
import { now, dateToLisbonTimezone } from "../../common/utils/date";
import moment from "moment";
import { sendStatusMessage } from "../../common/api/onesignal";
import { RootState } from "../RootReducer";

const getUser = (state: RootState) => state.user.user;

function* fetchAllOrders() {
  try {
    yield put(requestAllOrders());

    const resData = yield call(() =>
      firestore
        .collection("orders")
        .orderBy("orderNumber", "desc")
        .get()
        .then((querySnapshot) => {
          const resData: any = [];

          querySnapshot.forEach((documentSnapshot) => {
            resData.push({
              _id: documentSnapshot.id,
              ...documentSnapshot.data(),
            });
          });

          return resData;
        })
    );

    console.log(resData[0].orderNumber);
    yield put(requestAllOrdersSuccess(resData));
  } catch (error) {
    console.log(error);
    yield put(requestAllOrdersError());
  }
}

export function* watchFetchAllOrders() {
  yield takeLatest<AllOrdersAction>(FETCH_ALL_ORDERS, fetchAllOrders);
}

function* fetchSingleOrder(data: FetchSingleOrder) {
  try {
    yield put(requestSingleOrder());

    const resData = yield call(() =>
      firestore
        .collection("orders")
        .doc(data.orderId)
        .get()
        .then((docRef) => {
          return {
            _id: docRef.id,
            ...docRef.data(),
          };
        })
    );

    yield all(
      resData.products.map(function* (item: any, index: number) {
        yield call(() =>
          firestore
            .collection("products")
            .doc(item.product)
            .get()
            .then((res: any) => {
              resData.products[index] = {
                _id: res.id,
                count: resData.products[index].count,
                ...res.data(),
              };
            })
        );
      })
    );

    let promoObj;
    if (resData.promocode)
      promoObj = yield call(() =>
        firestore
          .collection("promocode")
          .doc(resData.promocode)
          .get()
          .then((res: any) => ({
            _id: res.id,
            ...res.data(),
          }))
      );

    let userData;
    if (resData.phoneNumber) {
      userData = yield call(() =>
        firestore
          .collection("additional_users")
          .where("phoneNumber", "==", resData.phoneNumber)
          .get()
          .then((querySnapshot) => {
            const resData: any = [];

            querySnapshot.forEach((documentSnapshot) => {
              resData.push({
                _id: documentSnapshot.id,
                ...documentSnapshot.data(),
              });
            });

            return resData.length ? resData[0] : null;
          })
      );
    }

    yield put(
      requestSingleOrderSuccess({
        ...resData,
        promocode: promoObj ? promoObj : resData.promocode,
        userDiscount: userData?.discount ?? null,
      })
    );
  } catch (error) {
    console.log(error);
    yield put(requestSingleOrderError());
  }
}

export function* watchFetchSingleOrder() {
  yield takeLatest<SingleOrderAction>(FETCH_SINGLE_ORDER, fetchSingleOrder);
}

function* fetchUpdateOrderStatus(data: FetchUpdateOrderStatus) {
  try {
    yield put(requestUpdateOrderStatus());

    const date = moment().format();

    // @ts-ignore
    const user = yield select(getUser);

    yield call(() =>
      firestore
        .collection("orders")
        .doc(data.orderId)
        .update({
          status: data.newStatus,
          ...(data.newStatus === "SENT" && { dateOfAccepted: date }),
          ...(data.newStatus === "CLOSE" && { dateOfClose: date }),
          ...(data.newStatus === "SENT" &&
            user.type !== "admin" && {
              pickedBy: {
                id: user.userId,
                name: user.name ?? "Name not found",
              },
            }),
          ...(data.newStatus === "CLOSE" &&
            user.type !== "admin" && {
              deliveredBy: {
                id: user.userId,
                name: user.name ?? "Name not found",
              },
            }),
        })
    );

    if (data.newStatus === "SENT") {
      sendStatusMessage(data.userId, "DELIVERING", data.orderId);
    }

    if (data.newStatus === "CLOSE") {
      sendStatusMessage(data.userId, "DELIVERED", data.orderId);
    }

    yield put(requestUpdateOrderStatusSuccess());
    yield put(fetchAllOrdersAction());
    yield put(fetchSingleOrderAction(data.orderId));
  } catch (error) {
    console.log(error);
    yield put(requestUpdateOrderStatusError());
  }
}

export function* watchFetchUpdateOrderStatus() {
  yield takeLatest<UpdateStatusOrderAction>(
    FETCH_UPDATE_ORDER_STATUS,
    fetchUpdateOrderStatus
  );
}
