import React, { FC, useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { connect } from "react-redux";
import { PrivateRoute } from "../../common/routes/PrivateRoute";
import { RootState } from "../../redux/RootReducer";
import { fetchAllOrders } from "../../redux/orders/actions";
import { fetchUpdateUser, userLogout } from "../../redux/user/actions";
import { Dispatch } from "redux";
import { firestore } from "../../common/firebase";
import notify from "../../assets/music/alert.wav";
import useAudio from '../../common/hooks/useAudio'
// const notify = require("../../assets/music/notification.mp3");
// import Cabinet from "../../pages/Cabinet/CabinetContainer";
import WarningOrderModal from "../WarningOrderModal";

const Cabinet = React.lazy(
  () => import("../../pages/Cabinet/CabinetContainer")
);

const LoginContainer = React.lazy(
  () => import("../../pages/Login/LoginContainer")
);

interface CabinetAppMainProps {
  user: any;
  fetchAllOrders: () => void;
  userLogout: () => void;
  fetchUpdateUser: (userId: string) => void;
  orders: any;
}

const CabinetAppMain: FC<CabinetAppMainProps> = ({
  user,
  fetchAllOrders,
  fetchUpdateUser,
  userLogout,
  orders,
}) => {
  const [ordersReady, setOrdersReady] = useState(false);
  const [audio, setAudio] = useState<any>()
  const [ordersLength, setOrdersLength] = useState<number>(0)
  const [warningVisible, setWarningVisible] = useState<boolean>(false)

  useEffect(() => {
    if (user) fetchUpdateUser(user._id);
  }, []);

  useEffect(() => {
    if (user) {
      if (user.lastLoginTime || user.passwordUpdated) {
        if (user.lastLoginTime < user.passwordUpdated?.seconds) {
          userLogout();
        }
      } else {
        userLogout();
      }
    }
  }, [user]);

  useEffect(() => {
    if (orders) {
      setOrdersReady(true);
    } else {
      fetchAllOrders();
      setOrdersReady(false);
    }
  }, [orders]);

  const { visible, actions: { pause } } = useAudio()

  return (
    <div className="App">
      <Switch>
        <PrivateRoute
          path="/cabinet"
          component={Cabinet}
          authed={user && user.type}
        />
        <Route exact path="/login" render={() => <LoginContainer />} />
        <Route path="*" render={() => <LoginContainer />} />
      </Switch>

      <WarningOrderModal 
        {...{ visible }}
        onClose={pause} 
      />
    </div>
  );
};

const mapStateToProps = (state: RootState) => {
  return {
    user: state.user.user,
    orders: state.orders.orders,
  };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  fetchAllOrders: () => dispatch(fetchAllOrders()),
  userLogout: () => dispatch(userLogout()),
  fetchUpdateUser: (userId: string) => dispatch(fetchUpdateUser(userId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(CabinetAppMain);
