import { getLocalStorage } from "../../common/auth/getStorage";
import {
  REQUEST_GET_USERS,
  REQUEST_GET_USERS_ERROR,
  REQUEST_GET_USERS_SUCCESS,
  REQUEST_SINGLE_USER,
  REQUEST_SINGLE_USER_ERROR,
  REQUEST_SINGLE_USER_SUCCESS,
  REQUEST_UPDATE_USER,
  REQUEST_UPDATE_USER_ERROR,
  REQUEST_UPDATE_USER_SUCCESS,
} from "./constants";
import { UsersState, UsersActionType } from "./types";

const initialState: UsersState = {
  loading: false,
  users: [],
  user: null,
};

export const usersReducer = (state = initialState, action: UsersActionType) => {
  switch (action.type) {
    case REQUEST_GET_USERS:
      return {
        ...state,
        loading: true,
      };
    case REQUEST_GET_USERS_ERROR:
      return {
        ...state,
        loading: false,
      };
    case REQUEST_GET_USERS_SUCCESS:
      return {
        ...state,
        loading: false,
        users: action.users,
      };
    case REQUEST_SINGLE_USER:
      return {
        ...state,
        loading: true,
      };
    case REQUEST_SINGLE_USER_ERROR:
      return {
        ...state,
        loading: false,
      };
    case REQUEST_SINGLE_USER_SUCCESS:
      return {
        ...state,
        loading: false,
        user: action.user,
      };
    case REQUEST_UPDATE_USER:
      return {
        ...state,
        loading: true,
      };
    case REQUEST_UPDATE_USER_ERROR:
      return {
        ...state,
        loading: false,
      };
    case REQUEST_UPDATE_USER_SUCCESS:
      return {
        ...state,
        loading: false,
      };
    case REQUEST_UPDATE_USER:
      return {
        ...state,
        loading: true,
      };
    case REQUEST_UPDATE_USER_SUCCESS:
      return {
        ...state,
        loading: false,
      };
    case REQUEST_UPDATE_USER_ERROR:
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
};
