import axios from "axios";

export const sendStatusMessage = async (
  userId: string,
  status: "DELIVERING" | "DELIVERED",
  orderId: string
) => {
  const STATUS_TITLES = {
    DELIVERING: {
      en: "Your order has been collected, a courier is coming !",
      pt: "O estafeta j est a caminho !",
    },
    DELIVERED: {
      en: "Your order has been delivered, thank you !",
      pt: "Seu pedido foi entregue, obrigado !",
    },
  };

  const ONESIGNAL_REST_API_KEY =
    "ZTAwMDdjN2UtMjI0OS00ZTRmLTg5ZGItOGExODVkNjYwZDQ5";
  const ONESIGNAL_APP_KEY = "a1a72bc2-1e53-40f9-a7ea-5958c854c8cb";

  await axios.post(
    "https://onesignal.com/api/v1/notifications",
    {
      app_id: ONESIGNAL_APP_KEY,
      contents: { en: STATUS_TITLES[status].en, pt: STATUS_TITLES[status].pt },
      include_external_user_ids: [userId],
      ios_badgeCount: 1,
      ios_badgeType: "Increase",
      data: {
        orderId,
      },
    },
    { headers: { Authorization: `Basic ${ONESIGNAL_REST_API_KEY}` } }
  );
};
