import { put, call, takeLatest } from "redux-saga/effects";
import {
  requestAuthUser,
  requestAuthUserError,
  requestAuthUserSuccess,
  requestUpdateUserSuccess,
} from "./actions";
import { FETCH_AUTH_USER, FETCH_UPDATE_USER } from "./constants";
import {
  AuthUserAction,
  FetchAuthUser,
  FetchUpdateUser,
  UpdateUserAction,
} from "./types";
import { auth, firestore } from "../../common/firebase";
import { getLocalStorage } from "../../common/auth/getStorage";

function* updateUserData(data: FetchUpdateUser) {
  try {
    const user = getLocalStorage("user");
    const updatedUser = yield call(() =>
      firestore
        .collection("additional_users")
        .doc(data.userId)
        .get()
        .then((userDoc) => ({
          _id: userDoc.id,
          ...userDoc.data(),
        }))
    );
    localStorage.setItem(
      "user",
      JSON.stringify({ ...updatedUser, lastLoginTime: user.lastLoginTime })
    );
    console.log(updatedUser);
    yield put(requestUpdateUserSuccess(updatedUser));
  } catch (error) {
    console.log(error);
  }
}

export function* watchUpdateUserData() {
  yield takeLatest<UpdateUserAction>(FETCH_UPDATE_USER, updateUserData);
}

function* fetchAuthUser(data: FetchAuthUser) {
  try {
    yield put(requestAuthUser());

    const { user } = yield call(() =>
      auth.signInWithEmailAndPassword(data.email, data.password)
    );

    const additional_user = yield call(() =>
      firestore
        .collection("additional_users")
        .where("userId", "==", user.uid)
        .get()
        .then((querySnapshot) => {
          const resData: any = [];

          querySnapshot.forEach((documentSnapshot) => {
            resData.push({
              _id: documentSnapshot.id,
              ...documentSnapshot.data(),
            });
          });

          return resData;
        })
    );

    const loginTime = new Date().getTime() / 1000;

    localStorage.setItem(
      "user",
      JSON.stringify({ ...additional_user[0], lastLoginTime: loginTime })
    );

    yield put(
      requestAuthUserSuccess({
        ...additional_user[0],
        lastLoginTime: loginTime,
      })
    );
    if (additional_user[0].type === "admin") {
      data.history.push("/cabinet");
    } else {
      data.history.push("/cabinet/orders");
    }

    data.onSuccess && data.onSuccess()
  } catch (error) {
    console.log(error);
    yield put(requestAuthUserError());

    // @ts-ignore
    data.onError && data.onError(error.message)
  }
}

export function* watchFetchAuthUser() {
  yield takeLatest<AuthUserAction>(FETCH_AUTH_USER, fetchAuthUser);
}
