import {
  FETCH_BLOCK_USER,
  FETCH_GET_USERS,
  FETCH_SINGLE_USER,
  FETCH_UPDATE_USER,
  REQUEST_BLOCK_USER,
  REQUEST_BLOCK_USER_SUCCESS,
  REQUEST_GET_USERS,
  REQUEST_GET_USERS_ERROR,
  REQUEST_GET_USERS_SUCCESS,
  REQUEST_SINGLE_USER,
  REQUEST_SINGLE_USER_ERROR,
  REQUEST_SINGLE_USER_SUCCESS,
  REQUEST_UPDATE_USER,
  REQUEST_UPDATE_USER_ERROR,
  REQUEST_UPDATE_USER_SUCCESS,
} from "./constants";

export const fetchBlockUser = (userId: string, blocked: boolean) => ({
  type: FETCH_BLOCK_USER,
  userId,
  blocked,
});

export const requestBlockUser = () => ({
  type: REQUEST_BLOCK_USER,
});

export const requestBlockUserError = () => ({
  type: REQUEST_BLOCK_USER,
});

export const requestBlockUserSuccess = () => ({
  type: REQUEST_BLOCK_USER_SUCCESS,
});

export const fetchGetUsers = () => ({
  type: FETCH_GET_USERS,
});

export const requestGetUsers = () => ({
  type: REQUEST_GET_USERS,
});

export const requestGetUsersError = () => ({
  type: REQUEST_GET_USERS_ERROR,
});

export const requestGetUsersSuccess = (users: any) => ({
  type: REQUEST_GET_USERS_SUCCESS,
  users,
});

export const fetchSingleUser = (userId: string) => ({
  type: FETCH_SINGLE_USER,
  userId,
});

export const requestSingleUser = () => ({
  type: REQUEST_SINGLE_USER,
});

export const requestSingleUserError = () => ({
  type: REQUEST_SINGLE_USER_ERROR,
});

export const requestSingleUserSuccess = (user: any) => ({
  type: REQUEST_SINGLE_USER_SUCCESS,
  user,
});

export const fetchUpdateUser = (
  userId: string,
  discount: number,
  history: any
) => ({
  type: FETCH_UPDATE_USER,
  userId,
  discount,
  history,
});

export const requestUpdateUser = () => ({
  type: REQUEST_UPDATE_USER,
});

export const requestUpdateUserError = () => ({
  type: REQUEST_UPDATE_USER_ERROR,
});

export const requestUpdateUserSuccess = () => ({
  type: REQUEST_UPDATE_USER_SUCCESS,
});
