import {
  FETCH_AUTH_USER,
  REQUEST_AUTH_USER,
  REQUEST_AUTH_USER_ERROR,
  REQUEST_AUTH_USER_SUCCESS,
  USER_LOGOUT,
  REQUEST_UPDATE_USER_SUCCESS,
  FETCH_UPDATE_USER,
} from "./constants";

export const fetchAuthUser = (
  email: string,
  password: string,
  history: any,
  onSuccess?: () => void,
  onError?: (err: any) => void,
) => ({
  type: FETCH_AUTH_USER,
  email,
  password,
  history,
  onSuccess,
  onError,
});

export const requestAuthUser = () => ({
  type: REQUEST_AUTH_USER,
});

export const requestAuthUserError = () => ({
  type: REQUEST_AUTH_USER_ERROR,
});

export const requestAuthUserSuccess = (user: any) => ({
  type: REQUEST_AUTH_USER_SUCCESS,
  user,
});

export const userLogout = () => ({
  type: USER_LOGOUT,
});

export const fetchUpdateUser = (userId: string) => ({
  type: FETCH_UPDATE_USER,
  userId,
});

export const requestUpdateUserSuccess = (user: any) => ({
  type: REQUEST_UPDATE_USER_SUCCESS,
  user,
});
